.result {
    width: 800px;
    margin: auto;
}

.rank {
    position: relative;
    display: inline-block;
    width: 50px;
    margin: 5px 10px 5px 10px;
    padding: 2px;
    left: -50px;
    top: 0px;
}

.score {
    position: relative;
    display: inline-block;
    width: 50px;
    height: 50px;
    left: 750px;
    top: 0px;
}

.title {
    position: relative;
    display: inline-block;
    width: 650px;
    text-align: left;
    vertical-align: top;
    left: -50px;
    top: -5px;
}

.info {
    position: relative;
    display: inline-block;
    width: 650px;
    text-align: left;
    vertical-align: top;
    font-style: italic;
    left: 15px;
    top: -20px;
}

.abstract {
    position: relative;
    display: inline-block;
    width: 650px;
    text-align: left;
    left: 15px;
    top: -30px;
}

.link {
    position: relative;
    display: inline-block;
    width: 200px;
    text-align: center;
    left: 0px;
    top: -30px;
}